<template>
    <div id="bookmarks">
        <div v-if="bookMarks.length >0">
            <ul class="music-list">
                <li class="music-item" v-for="(bookMark,index) in bookMarks  "
                :key="index"
                :class="checkActiveMusic(bookMark.index)? 'active-color':''"
                @click="selectMusic(bookMark);updateMusicListScroll($event);"
                :ref="checkActiveMusic(bookMark.index) ? 'activeMusic' : ''">
                
                    <div class="music-logo">
                        <fas icon="music"/>
                    </div>
                    <div class="music-info">
                        <span class="music-name">{{bookMark.name}}</span>
                        <span class="music-singer">{{bookMark.singer}}</span>

                    </div> 
                    <button class="music-status">
                        <fas icon ="pause" v-if="checkActiveMusic(bookMark.index) && getPlayingStatus" />
                        <fas icon ="play" v-else/>
                        


                        


                    </button>
                </li>
            </ul>
        </div>
        <div v-else>
            <div class="bookmarks-warning">
                <p>Bookmark musics is not available</p>
                <p>Music is playing from the general music list</p>
            </div>
        </div>
    </div>
</template>

<script>
import globalMixins from "@/mixins/globalMixins";
import musicMixins from "@/mixins/musicsMixins"

export default {
    name:"BookMarksPlaylist",
    mixins:[globalMixins ,musicMixins],
    data(){
        return{
            bookMarks:[],

        }
    },
    watch:{
        getBookMarksIndex:{
            handler(){
                this.updateBookMarks();
            }
        }
    },
    created(){
        this.updateBookMarks();
    },
    methods:{
        updateBookMarks(){
            this.bookMarks = this.getBookMarks();
        }
    }
}
</script>

