<template>
    <ul class="music-list" ref="musicList">
        <li class="music-item"
        v-for="(music,index) in getMusics" :key="index"
        :class="checkActiveMusic(music.index)? 'active-color':''"
        @click="selectMusic(music) ;updateMusicListScroll($event);"
        :ref="checkActiveMusic(music.index) ? 'activeMusic' : ''">
            <div class="music-logo">
                <fas icon="music" />
            </div>
            <div class="music-info">
                <span class="music-name">{{music.name}}</span>
                <div class="music-singer">{{music.singer}}</div>
            </div>
            <div class="music-status" >
                <fas icon="pause" v-if="checkActiveMusic(music.index) && getPlayingStatus"/>
                <fas icon="play" v-else/>
            </div>
        </li>
    </ul>
</template>

<script>
import globalMixins from "@/mixins/globalMixins";
import musicMixins from "@/mixins/musicsMixins"
export default {
    name:"AllMusicPlayList",
    mixins :[globalMixins, musicMixins]
}
</script>

