import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export const store = new Vuex.Store({
    state:{ 
        musics:[
            
            { index:0 ,name : 'Nàng Thơ' , file : require('@/assets/Musics/NangTho.mp3') , singer:'Hoàng Dung'},
            { index:1 ,name : 'Sài Gòn Đau Lòng Quá' , file : require('@/assets/Musics/SaiGonDauLongQua.mp3') , singer:'HKT - H.Duyên'},
            { index:2 ,name : 'Tay Nắm Tay Rời ' , file : require('@/assets/Musics/TayNamTayRoi.mp3') , singer:'PĐT Ngân'},
            
            { index:3 ,name : 'Anh Thề Đấy' , file : require('@/assets/Musics/Anh Thề Đấy.mp3') , singer:'Thanh Hưng Idol'},
            { index:4 ,name : 'Girls Like You' , file : require('@/assets/Musics/Girls Like You.mp3') , singer:'Maroon 5'},
            { index:5 ,name : 'Đường Ta Chở Em Về' , file : require('@/assets/Musics/Đường Tôi Chở Em Về.mp3') , singer:'Bùi Trường Linh'},
            { index:6 ,name : 'Sài Gòn Hôm Nay Mưa' , file : require('@/assets/Musics/Sài Gòn Hôm Nay Mưa.mp3') , singer:'Hoàng Duyên'},
            { index:7 ,name : 'Sang Xịn Mịn' , file : require('@/assets/Musics/Sang Xịn Mịn.mp3') , singer:'Gil'},
            

        ],
        bookMarksMusicsIndex : JSON.parse(localStorage.getItem('bookMarks')) || [],
        activeMusic:{index:-1 , music:''},
        isPlay:false,
        activePlayList:"all"
    },
    mutations:{
        setCurrentMusic: (state , newMusic) =>{
            state.activeMusic = newMusic
        },
        updatePlayStatus(state , status){
            state.isPlay = status;
        },
        addBookMarkMusic(state,index){
            state.bookMarksMusicsIndex.push(index)
            this.commit('updateBookMarksFromLocalStorage')
        },
        updatePlayList(state,playList){
            state.activePlayList = playList;
        },
        removeBookMarkMusic(state,index){
            state.bookMarksMusicsIndex = state.bookMarksMusicsIndex.filter(item =>item !==index)
            this.commit('updateBookMarksFromLocalStorage')
        },
        updateBookMarksFromLocalStorage(state){
            localStorage.setItem('bookMarks',JSON.stringify(state.bookMarksMusicsIndex))
        }
    },
    actions: {},
    modules: {}
});