<template>
    <section class="musics">
        <div class="musics-option">
            <button @click ="updatePlayList('all')"
                :class="checkActivePlaylist('all') ? 'btn-active-playlist' : ''">
                All Musics
            </button>
            <button @click="updatePlayList('bookmarks')" 
                :class="checkActivePlaylist('bookmarks') ? 'btn-active-playlist' : ''"
            >Bookmarks
            </button>
        </div>

        <template v-if="activePlayList =='all'">
            <AllMusicPlayList />
        </template>
        
        <template v-else-if="activePlayList == 'bookmarks'">
            <BookMarksPlayList />
        </template>
        

    </section>
</template>

<script>
import AllMusicPlayList from './AllMusicPlayList'
import BookMarksPlayList from './BookMarksPlayList'

export default {
    components: {
        AllMusicPlayList,
        BookMarksPlayList
    },
    data(){
        return{
            activePlayList: 'all',
        
        }
    },
    mounted(){},
    methods:{
        updatePlayList(playList){
            this.activePlayList = playList;
            this.$store.commit("updatePlayList",playList)
            
        },
        checkActivePlaylist(playlist){
            return playlist == this.activePlayList ;
        }

    }
}
</script>

